<template>
  <v-container>
    <form @submit.prevent="sendData">
      <h4 class="text-h4">Данные о пациенте</h4>
      <v-text-field label="Введите фамилию, имя, отчество больного" v-model="patient.fullName"></v-text-field>
      <v-text-field label="Введите номер истории болезни"></v-text-field>
      <h6 class="text-h6">Выберите пол</h6>
      <v-radio-group v-model="patient.gender">
        <v-radio label="мужской" value="male"></v-radio>
        <v-radio label="женский" value="female"></v-radio>
      </v-radio-group>

      <h4 class="text-h4">На каком этапе вы обратились к программе?</h4>
      <v-radio-group v-model="stage">
        <v-radio
          v-for="option in stageOptions"
          :key="option.id"
          :label="option.text"
          :value="option.value"
        ></v-radio>
      </v-radio-group>

      <v-switch
        v-if="beforeSurgery || afterSurgery"
        v-model="isBoneShortening"
        :label="`Устранение угловой деформации ${isBoneShortening ? 'с учетом' : 'без учета' } укорочения кости`"
      ></v-switch>

      <v-switch
        v-if="beforeSurgery || afterSurgery"
        v-model="boneRotationalDeformity"
        :label="`${boneRotationalDeformity ? 'У' : 'Не у'}странять ротационную деформацию кости`"
      ></v-switch>

      <v-switch
        v-if="beforeSurgery"
        v-model="planningOsteotomy"
        :label="`${planningOsteotomy ? 'П' : 'Не п'}роизводить остеотомию в зоне деформации`"
      ></v-switch>

      <v-switch
        v-if="!beforeSurgery"
        v-model="doneOsteotomy"
        :label="`Остеотомия в зоне деформации ${doneOsteotomy ? ' ' : 'не '}произведена`"
      ></v-switch>

      <h6 v-if="beforeSurgery && planningOsteotomy && !isBoneShortening" class="text-h6">Какая планируется остеотомия</h6>
      <v-radio-group
        v-if="beforeSurgery && planningOsteotomy && !isBoneShortening"
        v-model="osteotomyType"
      >
        <v-radio label="Шарнирная" value="articulated"></v-radio>
        <v-radio label="Поперечная" value="transverse"></v-radio>
      </v-radio-group>

      <h6 v-if="afterSurgery && doneOsteotomy" class="text-h6">Какая планируется остеотомия</h6>
      <v-radio-group
        v-if="afterSurgery && doneOsteotomy"
        v-model="osteotomyType"
      >
        <v-radio label="Шарнирная" value="articulated"></v-radio>
        <v-radio label="Поперечная" value="transverse"></v-radio>
      </v-radio-group>

      <v-text-field v-if="afterCorrection" label="За какой срок устранена угловая деформация (в днях)"></v-text-field>

      <h4 class="text-h4">Введите данные о больном</h4>
      <v-text-field
        v-model="patientCondition.deformedBoneLength"
        label="Длина деформированной кости - расстояние по прямой между суставными поверхностями (в мм)"
      ></v-text-field>
      <v-text-field
        v-model="patientCondition.symmetricalHealthyBoneLength"
        label="Длина симметричной здоровой кости (в мм)"
      ></v-text-field>
      <v-text-field
        v-model="patientCondition.lengthFromDeformationToArticularSurface"
        label="Длина одного из фрагментов или участка кости от вершины деформации до суставной поверхности (в мм)"
      ></v-text-field>

      <v-text-field
        v-model="patientCondition.widthOfBone"
        v-if="widthOfBoneInputEnabled"
        label="Толщина кости на вершине деформации (в мм)"
      ></v-text-field>

      <v-text-field
        v-model="patientCondition.deformationAngle"
        label="Величина угла деформации кости (в градусах)"
      ></v-text-field>

      <v-text-field
        v-model="patientCondition.rotationOfDeformedBoneAngle"
        v-if="boneRotationalDeformity"
        label="Величина угла ротации деформированной кости (в градусах, по ходу часовой стрелки - со знаком '+', против - со знаком '-')"
      ></v-text-field>

      <div v-if="afterSurgery">
        <h4 class="text-h4 mb-4">Данные об установленном на больном аппарате</h4>

        <p>С каким видом опоры соединены резбовые стержени, имеющие на противоположном конце шарнирный узел?</p>
        <v-radio-group v-model="deviceFeatures.supportType">
          <v-radio label="Дугой" value="arc"></v-radio>
          <v-radio label="Кольцом" value="circle"></v-radio>
        </v-radio-group>

        <p>Количество использованных резьбовых стержней</p>
        <v-radio-group v-model="deviceFeatures.numberOfRods">
          <v-radio label="Три" value="3"></v-radio>
          <v-radio label="Четыре" value="4"></v-radio>
        </v-radio-group>

        <div v-if="deviceFeatures.supportType === 'circle'">

          <p>Как Вы будете xарактеризовать местоположение каждого резьбового стержня на кольце?</p>
          <v-radio-group
            v-if="deviceFeatures.supportType === 'circle'"
            v-model="deviceFeatures.placeOfRods"
          >
            <v-radio
              label="по расстоянию между точкой пересечения перпендикуляра, проходящего через место расположения на кольце резьбового стержня, с плоскостью деформации и серединой костного фрагмента в плоскости того же кольца"
              value="length"
            ></v-radio>
            <v-radio label="по номерам отверстий (см. инструкцию)" value="numbers"></v-radio>
          </v-radio-group>

          <div v-if="deviceFeatures.placeOfRods === 'numbers'">
            <p>Аппарат Илизарова изготовлен на заводе:</p>
            <v-radio-group v-model="deviceFeatures.deviceFactory"
            >
              <v-radio
                label="Курганском"
                value="kurgan"
              ></v-radio>
              <v-radio label="Гудермесском" value="gudermes"></v-radio>
            </v-radio-group>

            <p
              v-if="deviceFeatures.deviceFactory === 'kurgan'"
            >
              Диаметр кольца аппарата Илизарова (в мм):
              <span class="blue--text">{{ deviceFeatures.diameter }}</span>
            </p>
            <v-slider
              v-if="deviceFeatures.deviceFactory === 'kurgan'"
              v-model="deviceFeatures.diameter"
              min="100"
              max="240"
              step="10"
            ></v-slider>

            <v-row>
              <v-col cols="12">
                <p>
                  Hомера отверстий на одном из двух ближайших к зоне деформации кольце, соединенном с резьбовыми стержнями, имеющими на противоположном конце шарнирный узел.
                </p>
                <p class="grey--text">Эти номера определяются количеством отверстий (включая "слепые") отсчитыванием их вдоль кольца по кратчайшему пути от отверстия "K" - ближайшего отверстия к месту пересечения кольца с диаметром, параллельным к плоскости угловой деформации, которое выбирается на удобной для отсчета половине кольца:</p>
                <p>а) номера отверстий ("A", "B", "C", "D"), занятых резьбовыми стержнями:</p>
              </v-col>
              <v-col
                v-for="(holeNumber, key) in deviceFeatures.holeNumber"
                :key="key"
                cols="6"
              >
                <v-text-field
                  :label="key.toUpperCase()"
                  v-model="deviceFeatures.holeNumber[key]"
                  v-if="key !== 'd' || deviceFeatures.numberOfRods === '4'"
                ></v-text-field>
              </v-col>
            </v-row>

            <p>
              б) ближайшего отверстия ("E") к месту пересечения кольца с перпендикуляром к плоскости деформации, проходящим через середину кости:
            </p>
            <v-text-field
              label="E"
              v-model="deviceFeatures.holeNumberE"
              :rules="[vnnofie]"
            ></v-text-field>

            <p>
              г) ближайшего отверстия ("G") к месту пересечения кольца с прямой, параллельной плоскости угловой деформации, и проходящей через середину кости:
            </p>
            <v-text-field
              label="G"
              v-model="deviceFeatures.holeNumberG"
            ></v-text-field>

            <p>
              в) ближайшего отверстия ("F") к месту пересечения кольца с диаметром, проходящим через середину кости и находящегося на большем расстоянии от кости:
            </p>
            <v-text-field
              label="F"
              v-model="deviceFeatures.holeNumberF"
              :rules="[vnnofie]"
            ></v-text-field>

            <p>
              Положение отверстия "K" - отверстие "K" обращено к поверхности
            </p>
            <v-radio-group v-model="deviceFeatures.holePositionK">
              <v-radio
                label="Вогнутой"
                value="concave"
              ></v-radio>
              <v-radio
                label="Выпуклой"
                value="convex"
              ></v-radio>
            </v-radio-group>

            <p v-if="boneRotationalDeformity">
              C каким кольцом соединены резьбовые стержни, имеющие шарнирные узлы c кольцом фиксирующим :
            </p>
            <v-radio-group
              v-if="boneRotationalDeformity"
              v-model="deviceFeatures.typeOfCircle"
            >
              <v-radio
                label="Проксимальный"
                value="proximal"
              ></v-radio>
              <v-radio
                label="Дистальный"
                value="distal"
              ></v-radio>
            </v-radio-group>

            <v-row>
              <v-col cols="12">
                <p>Введите расстояние между точкой пересечения перпендикуляра, проходящего через место расположения на опоре резьбового стержня с плоскостью деформации и серединой костного фрагмента в плоскости той же опоры (в мм): ( для резьбового стержня, находяшегося на вогнутой поверхности  со знаком "+", на выпуклой "-" ):</p>
              </v-col>
              <v-col
                v-for="(distance, key) in deviceFeatures.distanceBetweenCross"
                :key="key"
                cols="6"
              >
                <v-text-field
                  v-model="deviceFeatures.distanceBetweenCross[key]"
                  v-if="key !== 'd' || deviceFeatures.numberOfRods === '4'"
                  :label="key.toUpperCase()"
                ></v-text-field>
              </v-col>
            </v-row>

          </div>
        </div>
      </div>
      <div class="mt-10">
        <v-btn
          type="submit"
          block
          color="primary"
          @click.prevent="sendData"
        >Отправить данные</v-btn>
      </div>
    </form>
  </v-container>
</template>

<script>
export default {
  name: 'Form',

  data () {
    return {
      patient: {
        fullName: '', //fio
        diseaseHistoryNumber: '', //nib
        gender: 'male' //sex
      },
      stage: 'before_surgery', //pp
      stageOptions: [
        { id: 1, text: 'Предоперационном', value: 'before_surgery' },
        { id: 2, text: 'Послеоперационном', value: 'after_surgery' },
        { id: 3, text: 'После коррекции', value: 'after_correction' }
      ],

      isBoneShortening: false, //kzp
      boneRotationalDeformity: false, //bpkrin
      planningOsteotomy: false, //kvbp
      doneOsteotomy: false, //kvp

      osteotomyType: 'articulated', //kot

      patientCondition: {
        deformedBoneLength: '', //ddk
        symmetricalHealthyBoneLength: '', //dzk
        lengthFromDeformationToArticularSurface: '', //ddf
        widthOfBone: '', //tkvd
        deformationAngle: '', //vud
        rotationOfDeformedBoneAngle: '', //vur
      },

      deviceFeatures: {
        supportType: 'arc', //kvo
        numberOfRods: '3', //krs
        placeOfRods: 'length', //kvd
        deviceFactory: 'kurgan', //kz
        diameter: '100', //dk, cho
        holeNumber: {
          a: '', //noa
          b: '', //nob
          c: '', //noc
          d: '', //nod
        },
        holeNumberE: '', //noe
        holeNumberG: '', //nog
        holeNumberF: '', //nof
        holePositionK: 'concave', //ok
        typeOfCircle: 'proximal', //pd
        distanceBetweenCross: {
          a: '', //lnoa
          b: '', //lnob
          c: '', //lnoc
          d: '', //lnod
        }
      }
    }
  },

  computed: {
    beforeSurgery () {
      return this.stage === 'before_surgery'
    },

    afterSurgery () {
      return this.stage === 'after_surgery'
    },

    afterCorrection () {
      return this.stage === 'after_correction'
    },

    widthOfBoneInputEnabled () {
      return this.isBoneShortening ||
        !this.isBoneShortening && this.beforeSurgery && this.planningOsteotomy && this.osteotomyType === 'transverse' ||
        !this.isBoneShortening && this.afterSurgery && this.doneOsteotomy && this.osteotomyType === 'transverse' ||
        this.boneRotationalDeformity
    },

    cho () {
      const values = {
        '100': 32,
        '110': 34,
        '120': 36,
        '130': 38,
        '140': 42,
        '150': 44,
        '160': 46,
        '170': 48,
        '180': 52,
        '190': 54,
        '200': 56,
        '210': 58,
        '220': 62,
        '230': 64,
        '240': 68,
      }
      return values[this.deviceFeatures.diameter]
    },

    vnnofie () {
      const { deviceFactory, holeNumberE, holeNumberF } = this.deviceFeatures
      const kurgan = deviceFactory === 'kurgan'
      const gudermes = deviceFactory === 'gudermes'

      if (kurgan && holeNumberE < this.cho / 4 || gudermes && holeNumberF < 9) {
        if (
          kurgan && holeNumberF <= this.cho / 4 ||
          gudermes && holeNumberF <= 9
        ) {
          return 'Введен неправильный номер отверстия "E" или "F"'
        }
      }

      if (kurgan && holeNumberE > this.cho / 4 || gudermes && holeNumberF > 9) {
        if (
          kurgan && holeNumberF >= this.cho / 4 ||
          gudermes && holeNumberF >= 9
        ) {
          return 'Введен неправильный номер отверстия "E" или "F"'
        }
      }

      return true
    },

    config () {
      return {
        fio: this.patient.fullName,
        nib: this.patient.diseaseHistoryNumber,
        sex: this.patient.gender,
        pp: this.stage,
        kzp: this.isBoneShortening,
        bpkrin: this.boneRotationalDeformity,
        kvbp: this.planningOsteotomy,
        kvp: this.doneOsteotomy,
        kot: this.osteotomyType,
        ddk: this.patientCondition.deformedBoneLength,
        dzk: this.patientCondition.symmetricalHealthyBoneLength,
        ddf: this.patientCondition.lengthFromDeformationToArticularSurface,
        tkvd: this.patientCondition.widthOfBone,
        vud: this.patientCondition.deformationAngle,
        vur: this.patientCondition.rotationOfDeformedBoneAngle,
        kvo: this.deviceFeatures.supportType,
        krs: this.deviceFeatures.numberOfRods,
        kvd: this.deviceFeatures.placeOfRods,
        kz: this.deviceFeatures.deviceFactory,
        dk:this.deviceFeatures.diameter,
        cho: this.cho,
        noa: this.deviceFeatures.holeNumber.a,
        nob: this.deviceFeatures.holeNumber.b,
        noc: this.deviceFeatures.holeNumber.c,
        nod: this.deviceFeatures.holeNumber.d,
        noe: this.deviceFeatures.holeNumberE,
        nog: this.deviceFeatures.holeNumberG,
        nof: this.deviceFeatures.holeNumberF,
        ok: this.deviceFeatures.holePositionK,
        pd:this.deviceFeatures.typeOfCircle,
        lnoa: this.deviceFeatures.distanceBetweenCross.a,
        lnob: this.deviceFeatures.distanceBetweenCross.b,
        lnoc: this.deviceFeatures.distanceBetweenCross.c,
        lnod: this.deviceFeatures.distanceBetweenCross.d,
      }
    },
  },

  methods: {
    sendData () {
      const config = JSON.stringify(this.config)
      return window.fetch('http://localhost:3500/api/data/upload', {
        method: 'POST',
        headers: {
          'Content-type': 'application/json'
        },
        body: config
      })
        .catch(e => console.warn(e))
    }
  }
}
</script>